<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process229"></a>

<p><br></p>
<p><span class="rvts17">被遗忘者与被诅咒者（</span><span class="rvts50">The Forgotten and the Damned</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:368,Y:422</span><span class="rvts21">，第一处隐藏地下室</span>
<br><span class="rvts20">X:281,Y:398</span><span class="rvts21">，第二处隐藏地下室</span>
<br><span class="rvts20">X:255,Y:395</span><span class="rvts21">，第三处隐藏地下室</span>
<br><span class="rvts20">X:305,Y:395</span><span class="rvts21">，第四处隐藏地下室</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索血月岛的</span><span class="rvts20">4</span><span class="rvts21">个隐藏地下室</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><span class="rvts21">血月岛中部有</span><span class="rvts20">3</span><span class="rvts21">处雕像，破坏雕像的前提是先要找到一本叫做《华丽诗集》的书，这本书位于血月岛的很多地方。其中，小岛东北部有一处黑环势力占领的区域，有这本书。在血月岛大树的黑环团伙中，也有这本书。</span>
<br><span class="rvts21">另外，在接贾韩的支线</span><span class="rvts20"></span><span class="rvts21">邪恶之物的猎人（</span><span class="rvts20">A Hunter of Wicked Thing</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">的屋子内，也有这本书。这本书需要有</span><span class="rvts20"></span><span class="rvts21">学者</span><span class="rvts20"></span><span class="rvts21">标签的角色读懂后，才可以用于破除雕像的诅咒。</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1304.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">图为第一处隐藏地下室。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1305.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">图为第二处隐藏地下室。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1306.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">图为第三处隐藏地下室。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1307.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">第四处隐藏地下室位于血月岛的东海岸，有黑环士兵把守，打败他们，进入一个</span><span class="rvts20"></span><span class="rvts21">废旧的金矿</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts21">探索完所有的</span><span class="rvts20">4</span><span class="rvts21">处地下室后，该支线任务将关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



